import angular from 'angular';

import uiRouter from 'angular-ui-router';

angular.module('angular-aircraft', [uiRouter])
  .config(['$stateProvider', ($stateProvider) => {
    $stateProvider
      .state('home', {
        url: '',
        templateUrl: 'views/home.html'
      })
      .state('main', {
        url: '/',
        templateUrl: 'views/home.html'
      });
  }]);
